import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpErrorResponse, HttpParams } from '@angular/common/http';
import { Observable } from 'rxjs';
import { apiUrls } from 'src/environments/environment';
import { AuthService } from './auth.service';

@Injectable({
  providedIn: 'root'
})
export class ApisService {

  constructor(
    private http: HttpClient,
    private authService: AuthService,
  ) { }

  public sendOTPService(data): Observable<any> {
    return this.http.post(apiUrls.sendOtpURL, data, {
      headers: new HttpHeaders({
        'Content-Type': 'application/json',
      },)
    })
  }

  public verifyOTPService(data): Observable<any> {
    return this.http.post(apiUrls.verifyOtpURL, data, {
      headers: new HttpHeaders({
        'Content-Type': 'application/json',
      },)
    })
  }

  public updateAccountService(data): Observable<any> {
    return this.http.post(apiUrls.updateAccountURL, data, {
      headers: new HttpHeaders({
        'Content-Type': 'application/json',
        'token': this.authService.getSignupToken(),
      },)
    })
  }

  public loginWithPasswordService(data): Observable<any> {
    return this.http.post(apiUrls.loginWithPasswordURL, data, {
      headers: new HttpHeaders({
        'Content-Type': 'application/json',
      },)
    })
  }
  
}
