import { Component, OnInit, ElementRef, ViewChild } from '@angular/core';
import { FormControl, NgForm } from '@angular/forms';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { ApisService } from '../shared/services/apis.service';
import { AuthService } from '../shared/services/auth.service';

@Component({
  selector: 'app-home-page',
  templateUrl: './home-page.component.html',
  styleUrls: ['./home-page.component.css']
})
export class HomePageComponent implements OnInit {

  otpValue1: string;
  otpValue2: string;
  otpValue3: string;
  otpValue4: string;

  @ViewChild('otpbox1') otpbox1: ElementRef;
  @ViewChild('otpbox2') otpbox2: ElementRef;
  @ViewChild('otpbox3') otpbox3: ElementRef;
  @ViewChild('otpbox4') otpbox4: ElementRef;

  view_default : boolean; 
  view_signup : boolean;
  view_otp : boolean;
  view_form : boolean;
  view_msg : boolean;
  view_login : boolean;
  
  btnResend : boolean = false;
  countryCode : string = '1';
  phn : string;
  pwd : string;
  passEye : boolean = false;
  confPassEye : boolean = false;
  loginpass : boolean = false;
  qrAccLogin : boolean = true;


  constructor(
    private apiService: ApisService,
    private authService: AuthService,
    private toastr: ToastrService,
    public router: Router,
  ) { }

  ngOnInit(): void {
    if(this.authService.isLoggedIn()){
      this.router.navigate(['/home']);
    }
    this.viewToggle("default");
  }

  viewToggle(view){
    this.view_default = false; 
    this.view_signup  = false;
    this.view_otp  = false;
    this.view_form  = false;
    this.view_msg  = false;
    this.view_login  = false;

    switch(view){
      case "default" : { this.view_default = true; break;}
      case "signup" : { this.view_signup = true; break;}
      case "otp" : { this.view_otp = true; break;}
      case "form" : { this.view_form = true; break;}
      case "msg" : { this.view_msg = true; break;}
      case "login" : { this.view_login = true; break;}
    }    
  }

  otpTimerEvent(e){
    if(e.action=="done"){
      this.btnResend = true;
    }
  }

  // onOtpChange(e){
  //   if(e.length === 4){
  //     this.viewToggle("form");
  //   }
  // }

  loginWithPassword(phn:string, pwd:string) {
    this.phn = phn;
    this.pwd = pwd;
    const params = {
      'countryCode': this.countryCode,
      'mobileNumber': this.phn,
      'password': this.pwd,
    };
    this.apiService.loginWithPasswordService(params).subscribe((res)=> {
      this.toastr.success(res.msg);
      this.authService.setToken(res.token);
      this.authService.setUserName(res.data.name);
      this.authService.setUserId(res.data._id);
      this.authService.setUserProfileImg(res.data.profilePhoto);
      this.router.navigate(['/home']);
    }, (err) => {
      this.toastr.warning(err.error.msg);
    });
  }

  onSubmit(form:NgForm){ 
    if (form.value.password === form.value.confirmpassword) {
      const params = {
        'type':'signUpWithEmail',
        'name': form.value.name,
        'email': form.value.email,
        'password': form.value.password,
      }
      this.apiService.updateAccountService(params).subscribe((res)=> {
        this.toastr.success('Account created successfully');
        this.authService.setToken(res.token);
        this.authService.setUserName(res.data.name);
        this.authService.setUserId(res.data._id);
        this.authService.setUserProfileImg(res.data.profilePhoto);
        this.authService.removeSignupOTP();
        this.authService.removeSignupToken();
        this.router.navigate(['/home']);
      }, (err)=> {
        this.toastr.warning(err.error.msg);
      });
      // this.viewToggle("msg");
    } else {
      this.toastr.warning('Wrong Confirm Password');
    }
  }

  sendOTP(phn:string) {
    this.phn = phn;
    const params = {
      'countryCode': this.countryCode,
      'mobileNumber': this.phn,
    };
    this.apiService.sendOTPService(params).subscribe((res)=> {
      if (res.already_registered) {
        this.toastr.warning(res.msg);
        this.viewToggle('login');
      } else {
        this.toastr.success(res.msg);
        this.authService.setSignupOTP(res.otp);
        this.viewToggle('otp');
      }
    }, (err)=> {
      this.toastr.warning(err.error.msg);
    });
  }

  resendOTP(){
    const params = {
      "countryCode": this.countryCode,
      "mobileNumber": this.phn,
    };
    this.apiService.sendOTPService(params).subscribe((res)=> {
      if (res.already_registered) {
        this.toastr.warning(res.msg);
        this.viewToggle('login');
      } else {
        this.toastr.success(res.msg);
        this.authService.setSignupOTP(res.otp);
        this.otpValue1 = this.otpValue2 = this.otpValue3 = this.otpValue4 ='';
      }
    }, (err)=> {
      this.toastr.warning(err.error.msg);
    });
  }

  verifyOTP() {
    const params = {
      "countryCode": this.countryCode,
      "mobileNumber": this.phn,
      "otp":`${this.otpValue1}${this.otpValue2}${this.otpValue3}${this.otpValue4}`,
      "encryptedOTP": this.authService.getSignupOTP()
    };
    this.apiService.verifyOTPService(params).subscribe((res)=>{
      if(res.signup_completed) {
        this.toastr.warning(res.msg);
        this.viewToggle('login');
      } else {
        this.toastr.success('OTP verified successfully');
        this.authService.setSignupToken(res.token);
        this.viewToggle('form');
      }
    }, (err)=> {
      this.toastr.warning(err.error.msg);
    });
  }

  onCountryChange(obj) {
    this.countryCode = obj.dialCode;
  }

  eyeToggle(elm){
    if(elm==="pass"){ this.passEye?this.passEye=false:this.passEye=true }
    if(elm==="confpass"){ this.confPassEye?this.confPassEye=false:this.confPassEye=true }
    if(elm==="loginpass"){ this.loginpass?this.loginpass=false:this.loginpass=true }
  }
  confEyePassToggle(){
    if(this.confPassEye){
      this.confPassEye = false;
    }else{ 
      this.confPassEye = true;
    }
  }
  qrAccLoginDivToggle(){
    if(this.qrAccLogin){
      this.qrAccLogin = false;
    }else{ 
      this.qrAccLogin = true;
    }
  }
  

  otpInputChg(b){
    if(b==2){ this.otpbox2.nativeElement.focus(); }
    else if(b==3){ this.otpbox3.nativeElement.focus(); }
    else if(b==4){ this.otpbox4.nativeElement.focus(); }
    if(this.otpbox1.nativeElement.value 
      && this.otpbox2.nativeElement.value
      && this.otpbox3.nativeElement.value
      && this.otpbox4.nativeElement.value){
        this.verifyOTP();
      }
  }
  
}
